<template>
    <div>
        <Header/>
        <About/>
    </div>
</template>

<script>

    import Header from '@/components/Home/Header';
    import About from '@/components/Home/About';

    export default {
        name: "Home",
        components: {
            About,
            Header,
        },
        data() {
          return {
              loading: true,
              loaded: false,
              errored: false,
          }
        },
        created: {
            function() {
                this.axios
                    .get(/*process.env.VUE_APP_HOST + '/home'*/)
                    .then(response => {
                        this.resp = response.data.body
                        this.loaded = true
                        console.log(this.blogs)
                    })
                    .catch(error => {
                        console.log(error)
                        this.errored = true
                    })
                    .finally(() => this.loading = false)
            }
        }
    }
</script>

